// tslint:disable: no-any

/**
 * A basic implementation of the ExperimentationProvider interface used for testing
 */
class MsDyn365ExpTestConnector {
    public getConfigForClientSideInit(): any {
        return {};
    }
    public initializeClientSide(config: any): boolean {
        console.log(`Initialize client side called on MsDyn365ExpTestConnector with config ${config}`);
        return true;
    }
    public getExperiments(): any {
        return {
            name: 'msdyn365-exp-test-2',
            experiments: [
                {
                    id: 'expid1',
                    friendlyName: 'experiment-1',
                    status: 1,
                    variations: [
                        {
                            id: 'var1',
                            friendlyName: 'variant-1',
                            status: 1
                        },
                        {
                            id: 'var2',
                            friendlyName: 'variant-2',
                            status: 2
                        }
                    ]
                },
                {
                    id: 'expid1',
                    friendlyName: 'experiment-2',
                    status: 0,
                    variations: [
                        {
                            id: 'var3',
                            friendlyName: 'variant-3',
                            status: 0
                        }
                    ]
                }
            ]
        };
    }
    public getVariantsForUser(userId: string, attributes?: { [index: string]: string } | undefined): any {
        return [
            {
                variantId: 'var1',
                experimentId: 'expid1'
            }
        ];
    }
    public activateExperiment(userId: string, experiments: any, attributes?: { [index: string]: string } | undefined): boolean {
        console.log(
            `Active experiment called on MsDyn365ExpTestConnector with userId: ${userId}, experiments: ${JSON.stringify(
                experiments
            )}, attributes: ${JSON.stringify(attributes)}`
        );
        return true;
    }
    public initialize(config: any): boolean {
        console.log(`MsDyn365 Exp Test Connector called with config: ${JSON.stringify(config)}`);
        return true;
    }
}

const connector = new MsDyn365ExpTestConnector();
export default connector;
